<template>
  <div  class="btton-bar">
    <div class="check-content"  @click="adsa">
      <Check-button class="check-button"
                    :is-chack="isSeletAll"
      />全选
    </div>

    <div class="check-all" >
      合计{{totalPrice}}
    </div>
    <div class="currect-leat">
      去计算:{{checklength}}
    </div>
  </div>
</template>

<script>
  import CheckButton from "../../../components/public/checkbutton/CheckButton";

  export default {
    name: "CartButtonBar",
    components:{
      CheckButton
    },
    methods:{
      adsa(){
        if(this.isSeletAll){
          this.cartList.forEach(item=>{
            return item.checked = false
          })
        }else {
          this.cartList.forEach(item=>{
              return item.checked = true
          })
        }
      },
    },
    computed:{
      totalPrice(){
        return "¥" + this.$store.state.cartList.filter((item)=>{
          // console.log(item)
          return item.checked
        }).reduce((pre,item)=>{
          let price = item.newpress.replace('¥','')*1;
          // console.log(price)
          return pre+ price * item.count
        },0).toFixed(2)
      },
      checklength(){
        return this.$store.state.cartList.filter(item=>item.checked).length;
      },
      isSeletAll(){
        if(this.$store.state.cartList.length===0) {
          return false

        }else {
          return  !(this.$store.state.cartList.filter(item=>!item.checked).length)
        }
      }
    },
  }
</script>

<style scoped>
  .btton-bar{
    display: flex;
    height: 49px;
    position: relative;
    line-height: 49px;
    padding-left: 10px;

  }
  .check-content{
    display: flex;
    align-items: center;
  }
  .check-button{
    width: 20px;
    height: 20px;
    line-height: 20px;
  }
  .check-all{
    padding-left: 20px;
    flex: 1;
  }
  .currect-leat{
    width: 80px;
    background: #ff8c13;
    color: white;
    padding-right: 10px;
    text-align: center;
  }
</style>