<template>
  <div class="cart" >
    <Scroll class="scroll" ref="scroll">
      <Cart-list-item v-for="(item,index) in cartList"
                      :itemInfo="item"
                      :key="index"/>
    </Scroll>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import Scroll from "../../../components/public/scroll/Scroll";
  import CartListItem from "./CartListItem";
  export default {
    name: "CartList",
    components:{
      Scroll,
      CartListItem
    },
    computed:{
      ...mapGetters(['cartList'])
    },
    activated() {
    this.$refs.scroll.refresh();
    }
  }
</script>

<style scoped>
.scroll{
  height: calc(100vh - 44px - 97px);
}
</style>